import React, { Component } from 'react';
import contactapi from '../contactapi.js';
import './Insert.css';

function getUrlDomain(string) {
    const str = string;
    const res = str.split("/");
    if(res[0] === "https:" || res[0] === "http:") {
        res.splice(0, 2);
        res.splice(1, 100);
    }
    else {
    	res.splice(1, 100);
    }
    const laststring = res[0].split(".");
    if(laststring.length > 2) {
    	laststring.splice(0, 1);
    }
    return laststring[0];
}

class Insert extends Component {
    constructor(props) {
        super(props);
        this.state = {
            url: "",
            id: "",
            password: ""
        }
    }

    urlChange = (e) => {
        this.setState({
            url: e.target.value
        })
    }
    idChange = (e) => {
        this.setState({
            id: e.target.value
        })
    }
    passwordChange = (e) => {
        this.setState({
            password: e.target.value
        })
    }
    insertPost = () => {
        if(!this.state.url || !this.state.id || !this.state.password) {
            return alert("  .");
        }
        const id = window.sessionStorage.getItem("Reid");
        const url = getUrlDomain(this.state.url);
        const isConfirm = true//confirm(" URL      .\nid: " + this.state.id + ", password: " + this.state.password + "\n ?");
        if(isConfirm) {
            contactapi(url, id, this.state.id, this.state.password)
            .then((res) => {
                if(res.data.error === "true") {
                    return alert(res.data.words);
                }
                else {
                    window.location.reload(false);
                    return alert(" .")
                }
            })
        }
    }


    render() {
        return(
            <div>
                <div className="insert-body">
                    <form className="form">
                        <h1 className="insert-head-text"></h1>
                        <hr/>
                        <div className="url-input">
                            <label> URL</label>
                            <p className="url-text">URL    .</p>
                            <input onChange={this.urlChange} className="url" type="text" placeholder="URL"/>
                        </div>
                        <div className="input">
                            <label></label>
                            <input onChange={this.idChange} className="id" type="text" placeholder="ID"/>
                        </div>
                        <div className="input">
                            <label></label>
                            <input onChange={this.passwordChange} className="password" type="password" placeholder="Password" /> 
                        </div>
                        <div className="input">
                            <button onClick={() => this.insertPost()}></button>
                        </div>
                    </form>
                </div>
            </div>
        )
    }
}

export default Insert;


